import React from 'react'
import styled from 'styled-components'

export const Cursor = ({x, y}) => {
    return (
        <CursorDot x={x} y={y}/>
    )
}
const CursorWrapper = styled.div.attrs(props => ({
    style: {
      top: props ? props.y : 0,
      left: props ? props.x : 0
        },
    }))`
    width: 8px;
    height: 8px;
    position: absolute;
    transform: translate(-50%, -50%);
    background: black;
    z-index: 15;
    border-radius: 100%;
    pointer-events: none;
    overflow: auto;
    mix-blend-mode: difference;
`
const CursorDot = styled.div.attrs(props => ({
    style: {
        transform: `translate(${props.x - 20}px, ${props.y - 20}px)`
    },
    }))`
    width: 36px;
    height: 36px;
    border-radius: 100%;
    border: 2px solid black;
    transition: transform 0.1s ease-out;
    pointer-events: none;
    transform: translate(-50%, -50%);
    z-index: 15;
    position: absolute;
`