import React, { useState, useEffect } from 'react'
import styled from 'styled-components'
import { Cursor } from './cursor'

export const CursorProvider = ({ children }) => {
    useEffect(() => {
        {window.innerWidth < 600 ? setVisibility(false) : setVisibility(true)}
    })
    
    const [cursor, setCursor] = useState({x: 0, y: 0})
    const [visibility, setVisibility] = useState(true)


    return (
    <CursorWrapper onMouseLeave={(e) => setVisibility(false)} onMouseEnter={(e) => setVisibility(true)} onMouseMove={(e) => setCursor({x: e.pageX, y: e.pageY})}>
        {visibility ? <Cursor x={cursor.x} y={cursor.y}/> : null}
        {children}
    </CursorWrapper>)
}

const CursorWrapper = styled.div`
    width: 100vw;
    height: 100vh;
`